import CommonTitle from "components/CommonTitle"
export default {
    props: ["title","placeholderTip","value","name","isSelect","options"],
    components:{
        CommonTitle
    },
    watch:{
        value: function(to){
            this.innerVal = to;
        }
    },
    methods: {
        myChange: function(e){
            this.innerVal = e.target.value;
            this.$emit("updateFormVals",{
                name:this.name,
                value: e.target.value
            });
        }
    },
    data(){
        return {
            innerVal: this.value || ""
        }
    }
}



